.api {
    display: grid;
    grid-template-columns: 200px 1fr;
    text-align: center;
    grid-gap: 3px;
    & p {
        font-size: 2vw;
        padding: 0.5em;
        background: #a39696;
        text-align: center;
        color: #312f2f;
        transition: background 0.3s;
        &:hover {
            background: #978b8b;
            cursor: pointer;
        }
    }
}

.home-page {
    padding: 30px;
}
