@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.contact{display:flex;flex-direction:column;flex-wrap:wrap;width:300px;font-size:11px;height:72px;background:#8c8383;border:1px solid white;margin:10px;padding:8px;border-radius:10px;color:white;-webkit-transition:-webkit-transform 0.3s;transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s;box-shadow:0px 0px 5px 2px rgba(0,0,0,0.12)}.contact__img{width:72px;border-radius:100px;border:1px solid white}.contact p{position:relative;right:0;float:right}.contact__info{display:flex;flex-direction:column}.contact:hover{-webkit-transform:scale(1.03);transform:scale(1.03);cursor:pointer}

.lds-ellipsis{display:block;position:relative;width:85px;height:300px;margin:10% auto}.lds-ellipsis div{position:absolute;top:33px;width:16px;height:16px;border-radius:50%;background:#5e5353;-webkit-animation-timing-function:cubic-bezier(0, 1, 1, 0);animation-timing-function:cubic-bezier(0, 1, 1, 0)}.lds-ellipsis div:nth-child(1){left:8px;-webkit-animation:lds-ellipsis1 0.6s infinite;animation:lds-ellipsis1 0.6s infinite}.lds-ellipsis div:nth-child(2){left:8px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(3){left:32px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(4){left:56px;-webkit-animation:lds-ellipsis3 0.6s infinite;animation:lds-ellipsis3 0.6s infinite}@-webkit-keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}@keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}@-webkit-keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(24px, 0);transform:translate(24px, 0)}}@keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(24px, 0);transform:translate(24px, 0)}}

.contacts-list{display:flex;flex-wrap:wrap;justify-content:center;max-width:1000px;margin:auto}

.api{display:grid;grid-template-columns:200px 1fr;text-align:center;grid-gap:3px}.api p{font-size:2vw;padding:0.5em;background:#a39696;text-align:center;color:#312f2f;-webkit-transition:background 0.3s;transition:background 0.3s}.api p:hover{background:#978b8b;cursor:pointer}.home-page{padding:30px}

.user-data{text-align:center}.user{width:80%;text-align:center;margin:auto;border:1px solid #382828;background:#493939c7;border-radius:15px;padding:20px;color:#a59f9f;display:flex;justify-content:center}.user__info{display:grid;grid-template-columns:90px 1fr;padding:15px;border-radius:0 20px 20px 0;background:rgba(133,132,132,0.287);text-align:left;background:#564040a6}.user__image{border-radius:20px  0 0 20px}.user__label{text-align:right;margin-right:10px}

.form{padding:30px 10px 30px 0;border:1px solid #ffffff;background:#4f4242;border-radius:5px;color:#968888;width:400px;margin:auto;position:relative;text-align:center}input{background:#ffffff2b;border:none;padding:4px;outline:none;color:#8f8888;box-sizing:border-box;border:1px solid transparent;-webkit-transition:border 0.2s, background 0.3s;transition:border 0.2s, background 0.3s}input:focus{border:1px solid rgba(46,25,25,0.671)}input:hover{background:#ffffff38}.button{margin-top:10px;background:#6d6262;border:#ffffff;padding:8px 15px;font-size:14px;border-radius:3px;color:#ffffff;-webkit-transition:background 0.3s, border 0.3s;transition:background 0.3s, border 0.3s;border:1px solid transparent;position:relative;left:130px;top:15px;margin-left:10px}.button:hover{background:#2e2020;border:1px solid #978c8ca1}#login{margin-left:40px}#password{margin-left:3px}#confirm-password{margin-left:16px}.label{width:280px;display:block;margin:0 auto 10px}.label:first-child{margin-bottom:10px}.login-facebook{background:url(data:image/png;base64,);width:35px;height:35px;background-repeat:no-repeat;background-size:35px;background-position-y:-1px;outline:none;cursor:pointer;position:relative;border:1px solid transparent;border-radius:1px;box-sizing:border-box;-webkit-transition:border 0.3s;transition:border 0.3s;position:relative;border-radius:5px;left:-87px;top:25.5px}.login-facebook:hover{border:1px solid #7e7777a1;border-radius:5px}.error{color:#d86c6c;text-align:center}

body{margin:0;padding:0;font-family:"Montserrat", sans-serif;background:#b3a9a9a9}p{margin:0;padding:0}h1{text-align:center}button{outline:none;cursor:pointer}.nav{background:#e8e8e8;position:relative;width:100%;display:flex;justify-content:center;padding:10px 0}.nav-link{text-decoration:none;width:100px;text-align:center;background:rgba(63,48,48,0.397);color:#3f3f3f;border:1px solid transparent;margin-right:20px;padding:5px;border-radius:4px;-webkit-transition:background 0.3s, border 0.3s, color 0.3s, -webkit-transform 0.3s;transition:background 0.3s, border 0.3s, color 0.3s, -webkit-transform 0.3s;transition:background 0.3s, border 0.3s, color 0.3s, transform 0.3s;transition:background 0.3s, border 0.3s, color 0.3s, transform 0.3s, -webkit-transform 0.3s}.nav-link:hover{background:white;border:1px solid rgba(17,11,11,0.575);color:rgba(17,11,11,0.767)}.nav-link:hover:active{-webkit-transform:scale(0.98);transform:scale(0.98)}.active{background:#6f6363;color:white}.logout{background:url(data:image/png;base64,);width:35px;height:35px;border:none;background-repeat:no-repeat;background-size:27px;background-position-y:4px;outline:none;cursor:pointer;position:absolute;right:0;top:8px;-webkit-transition:-webkit-transform 0.3s;transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s}.logout:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.logout:active{-webkit-transform:scale(0.9);transform:scale(0.9)}@media (max-width: 500px){.user{font-size:0.6em;width:100%;text-align:center;margin:auto;background:#8f8f8fc7;border-radius:10px;border:none;padding:31px 0;color:#a59f9f;display:flex;justify-content:center}.user__info{grid-template-columns:60px 1fr;text-align:left;padding:15px 0;padding-right:10px}.user__label{text-align:center;margin-right:3px}.nav .nav-link{width:66px;font-size:13px}#root .form{width:92%}.form .button{left:70px}.form .label{width:300px}#login{width:150px;margin-left:37px}#password{width:150px}#confirm-password{margin-left:14px;width:150px}#root .login-facebook{left:-140px;top:25.5px}.api{grid-template-columns:300px}.api p{font-size:3vw;background:transparent}}

.user-id{background-color:#5a45456e;padding:5px;text-align:center;margin:5px auto;color:white;position:relative}.user-id span{position:absolute;left:0px;top:1px;background:#58373757;width:18px;height:17px;padding:5px}

.add-comment{background-color:white;border:1px solid #5d4b4b38;box-shadow:0px 0px 5px 2px rgba(0,0,0,0.25)}.add-comment__textarea{height:2em;width:300px;margin:10px 0;outline:none;border:1px solid #5d4b4b38;background-color:#ebebeb;-webkit-transition:background 0.3s;transition:background 0.3s;color:#9f9393}.add-comment__textarea:hover{background-color:white}

.edit-comment{overflow:auto;width:95%;height:73px;border:none;outline:none}

.post-id{position:absolute;height:50px;width:50px;display:flex;justify-content:center;align-items:center;background-color:#afa4a4;color:white;left:0;top:0}.flex-wrapper{display:flex;justify-content:flex-end}

.MuiGrid-root{padding:2% 20% 20% 20%}.makeStyles-root-278 img{border-radius:32px}

